/**
 * @hidden
 */
const update: (arr: number[], idx: number, value: number) => number[] =
    (arr, idx, value) => ([
        ...arr.slice(0, idx + 1),
        ...(arr.slice(idx + 1).map(x => x + value))
    ]);

/**
 * @hidden
 */
export class RowHeightService {
    private offsets: Array<number> = [];
    private heights: Array<number> = [];

    constructor(private total: number = 0, private rowHeight: number, private detailRowHeight: number) {
        let agg: number = 0;
        for (let idx: number = 0; idx < total; idx++) {
            this.offsets.push(agg);
            agg += rowHeight;
            this.heights.push(rowHeight);
        }
    }

    public height(rowIndex: number): number {
        return this.heights[rowIndex];
    }

    public expandDetail(rowIndex: number): void {
        if (this.height(rowIndex) === this.rowHeight) {
            this.updateRowHeight(rowIndex, this.detailRowHeight);
        }
    }

    public collapseDetail(rowIndex: number): void {
        if (this.height(rowIndex) > this.rowHeight) {
            this.updateRowHeight(rowIndex, this.detailRowHeight * -1);
        }
    }

    public index(position: number): number {
        if (position < 0) {
            return undefined;
        }

        const result: number = this.offsets.reduce((prev, current, idx) => {
            if (prev !== undefined) {
                return prev;
            } else if (current === position) {
                return idx;
            } else if (current > position) {
                return idx - 1;
            }
            return undefined;
        }, undefined); // tslint:disable-line:align
        return result === undefined ? this.total - 1 : result;
    }

    public offset(rowIndex: number): number {
        return this.offsets[rowIndex];
    }

    public totalHeight(): number {
        return this.heights.reduce((prev, curr) => prev + curr, 0);
    }

    private updateRowHeight(rowIndex: number, value: number): void {
        this.heights[rowIndex] += value;
        this.offsets = update(this.offsets, rowIndex, value);
    }
}





